/**
*simulate api that returns data.
* @param 'index(Number)' start index of data to be fetched, 'count(Number)' total data points to be fetched.
* @return [data].
*/
export const getdata = (index,count)=> {
    return  {
        data:data.slice(index,index+count),
        totallength:data.length
    }
}
/**
*sorts the array data with given key.
* @param [data] to be sorted and {key:'',rev:''} sort object.
* @return [sorted] sorted data.
*/
export const sortdata = (data , sort) => data.sort((a,b) => {
        let ret = (a[sort.key] >= b[sort.key])?1:-1;
        ret = sort.rev?-ret:ret;
        return ret
    });
